    body {
        margin: 0;
        padding: 0;
        font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
        background-color: #F7F7F9;
    }
    
    .container {
        width: 100%;
        background-color: transparent;
        box-sizing: border-box;
        font-weight: 600;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-size: 15px;
        display: inline-block;
    }
    
    h1 {
        display: inline-block;
        font-weight: 600;
        font-size: 30px;
        box-sizing: border-box;
        margin: 10px;
    }
    
    form {
        display: inline-block;
        box-sizing: border-box;
        width: 100%;
    }
    
    input {
        width: 356px;
        height: 46px;
        border-radius: 5px;
        box-sizing: border-box;
        padding-left: 55px;
        border-width: thin;
        border: 1px solid lightgray;
    }
    
    input::placeholder {
        font-family: sans-serif;
        font-weight: 500;
        font-size: 15px;
        color: gray;
    }
    
    .search {
        background-image: url(./search.svg);
        background-repeat: no-repeat;
        background-position: 10px;
    }
    
    .inboxContainer {
        width: 100%;
        height: auto;
        display: inline-block;
        box-sizing: border-box;
        margin-top: 30px;
        padding: 10px;
    }
    
    .eMail {
        width: 100%;
        height: 120px;
        box-sizing: border-box;
        display: inline-block;
        background-color: rgb(255, 255, 255);
        margin-bottom: 2px;
    }
    
    .date {
        width: 75px;
        height: 15px;
        box-sizing: border-box;
        display: inline-block;
        color: gray;
        padding-left: 15px;
        position: relative;
        top: 73px;
        left: 270px;
    }
    
    .username {
        width: 100%;
        height: 15px;
        box-sizing: border-box;
        position: relative;
        top: 3px;
        display: inline-block;
    }
    
    .subject {
        width: 100%;
        height: 15px;
        display: inline-block;
        position: relative;
        top: 4px;
    }
    
    .contentMail {
        width: 100%;
        height: 15px;
        display: inline-block;
        color: gray;
        font-weight: 400;
        position: relative;
        box-sizing: border-box;
    }
    
    .favImg,
    .favedImg {
        background: url(./star.svg) no-repeat center;
        width: 60px;
        height: 25px;
        margin: 0 3px 0 10px;
        position: relative;
        top: -65px;
        left: 200px;
        display: inline-block;
        cursor: pointer;
        border: none;
    }
    
    .favedImg {
        background: url(./starFilled.svg) no-repeat center;
    }
    
    .deleteImg {
        background: url(./delete.svg) no-repeat center;
        width: 50px;
        height: 23px;
        position: relative;
        top: -65px;
        left: 200px;
        display: inline-block;
        cursor: pointer;
        border: none;
        box-sizing: border-box;
    }
    
    .overlay {
        position: fixed;
        width: 100%;
        height: 100%;
        margin: 0;
        top: 0;
        left: 0;
        visibility: hidden;
        color: white;
        background-color: rgba(0, 0, 0, 0.8);
    }
    
    .dialog {
        width: 100%;
        margin: 0;
        visibility: hidden;
        z-index: 10;
        background-color: white;
        border-radius: 5px;
        text-align: center;
    }
    
    .dialogTitle {
        box-sizing: border-box;
        position: absolute;
        left: 42px;
        top: 222px;
        font-size: 22px;
        color: white;
    }
    
    .deleteMail {
        border: 1px solid silver;
        width: 150px;
        height: 50px;
        box-sizing: border-box;
        position: absolute;
        top: 301px;
        left: 95px;
        font-size: 18px;
        border-radius: 5px;
    }
    
    .dontDeleteMail {
        border: 1px solid silver;
        width: 150px;
        height: 50px;
        box-sizing: border-box;
        position: absolute;
        top: 360px;
        left: 95px;
        font-size: 18px;
        border-radius: 5px;
    }
    
    .deleteMail:hover,
    .dontDeleteMail:hover {
        background-color: #0591FD;
        color: white;
    }
    
    body.dialog-visible .overlay,
    body.dialog-visible .dialog {
        /*body'de dialog-visible varsa overlay görünür olucak.*/
        visibility: visible;
    }
    
    .error {
        width: 200px;
        height: 50px;
        position: absolute;
        top: 200px;
        left: 100px;
    }
